.gatorX_main {
  margin-top: 100px;
}
.flexContainer0 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: auto;
  width: 100vw;
}

.gridContainer0 {
  display: grid;
  grid-template-columns: 50% 50%;
  align-items: flex-start;
}

.gridContainer0 > * {
  box-sizing: border-box;
  /* Add any additional styling for grid items here */
  padding: 3.5vw 1.5vw;
  text-align: center;
  font-size: 2vw;
}

.headerContainer {
  position: relative;
  width: 100vw;
}
.headerImage {
  width: 100%;
}

.logoImage {
  position: absolute;
  background-color: black;
  transform: translate(0%, -85%);
  text-align: center;
  padding: 1.5vw 8vw;
  padding-bottom: 0;
}

.gatorXTitle {
  font-size: 5vw;
  line-height: 5vw;
}
.sloganLabel {
  font-size: 3vw;
  line-height: 3.5vw;
}
.money {
  /* line-height: 8vw; */
  margin-top: 1vw;
}
.lightningLogo,
.lightningLogo2 {
  position: absolute;
  transform: translate(-120%, -160%);
  filter: brightness(0) invert(1);
  width: 5vw;
}
.lightningLogo2 {
  transform: translate(770%, -160%);
}

.xprizeQuote {
  width: 100%;
}
.Quote,
.Quote2,
.Quote3 {
  position: absolute;
  color: white;
  transform: translate(0%, -170%);
  text-align: center;
  font-size: 2.7vw;
  padding: 0 15vw;
  text-transform: uppercase;
  font-weight: 600;
}
.Quote2 {
  font-size: 2.1vw;
  transform: translate(45%, -600%);
}
.Quote3 {
  font-size: 1.2vw;
  transform: translate(53%, -850%);
  text-transform: none;
  font-weight: 100;
}

.flexContainer1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
}

.gridContainer1 {
  display: grid;
  grid-template-columns: 50% 50%;
  align-items: flex-start;
}

.gridContainer1 > * {
  box-sizing: border-box;
  /* Add any additional styling for grid items here */
  padding: 3.5vw 3vw;
  text-align: left;
  font-size: 2vw;
}

.moreInformation {
  font-size: 2vw;
  padding-top: 0;
  padding-left: 3vw;
  padding-bottom: 0;
  line-height: 2vw;
  color: grey;
}
.xprizeLink {
  font-size: 2vw;
  padding-top: 0;
  line-height: 2vw;
  padding-left: 3vw;
  color: orange;
}
.gridContainer2 {
  width: 90vw;
  margin: 3vw;
  margin-top: 4vw;
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  gap: 2vw;
}

.gridContainer2 img {
  width: 100%;
  height: auto;
}

.flexContainer2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  gap: 5vw;
  margin-bottom: 6vw;
}
.gatorXPartnersTitle,
.gatorXInternTeam {
  width: 50vw;
  height: 8vw;
  color: white;
  font-size: 4vw;
  text-align: center;
  padding: 1vw 1vw;
  border: 1px solid;
}
.flexContainer2-Row,
.flexContainer2-Row1 {
  width: 100%;
  display: grid;
  grid-template-columns: 4fr 1fr;
  align-items: center;
  gap: 2vw;
  padding: 0 3vw;
}

.flexContainer2-Row img,
.flexContainer2-Row1 img {
  border-radius: 20px;
}

.flexContainer2-Row1 {
  grid-template-columns: 1fr 2fr;
}
.flexContainer3 {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
}

.interndescription {
  color: white;
  font-size: 2vw;
  text-align: center;
  padding: 8vw 3vw 0vw 3vw;
}
.teamList {
  color: white;
  font-size: 2vw;
  text-align: center;
  padding: 6vw 3vw 0vw 3vw;
}

.gridContainer3 {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  align-items: center;
}

.gridContainer4 {
  padding: 3vw 1vw;
  padding-left: 3vw;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.teamListTitle {
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  font-size: 2vw;
}

.applyButton {
  width: 25vw;
  color: orange;
  font-size: 2vw;
  text-align: center;
  padding: 1vw 1vw;
  border: 1px solid;
  margin-top: 2vw;
}

.slick-slider {
  height: 50vh;
  width: 80vw;
  max-height: 50vw;
  max-width: 80vw;
  margin: 1rem;
}
@media (max-width: 768px) {
  .gatorX_main {
    margin-top: 60px;
  }
  .logoImage {
    transform: translate(30%, -85%);
    width: 60vw;
  }
  .lightningLogo {
    position: absolute;
    transform: translate(-120%, -140%);
  }
  .lightningLogo2 {
    transform: translate(900%, -140%);
  }
  .gridContainer0 > * {
    padding: 3vw 0vw;
    text-align: center;
    font-size: 3vw;
    line-height: 1.5;
  }
  .Quote {
    transform: translate(0%, -130%);
    text-align: center;
    font-size: 3.9vw;
    padding: 0 5vw;
    text-transform: uppercase;
    font-weight: 600;
  }
  .Quote2 {
    font-size: 3.2vw;
    transform: translate(25%, -250%);
  }
  .Quote3 {
    font-size: 2.8vw;
    transform: translate(15%, -180%);
    text-transform: none;
    font-weight: 100;
  }
  .gridContainer1 > * {
    padding: 3.5vw 3vw;
    font-size: 2.8vw;
    line-height: 1.5;
  }
  .moreInformation,
  .xprizeLink {
    font-size: 3vw;
    line-height: 3vw;
  }
  .gridContainer2 {
    display: none;
  }

  .gatorXPartnersTitle {
    width: 60vw;
    height: 9vw;
    font-size: 5vw;
    padding: 1vw 1vw;
  }
  .gridContainer3 {
    width: 100%;
    display: grid;
    grid-template-columns: 0fr 1fr 0fr;
    align-items: center;
  }
  .gatorXInternTeam {
    width: 65vw;
    height: 9.5vw;
    font-size: 5vw;
    padding: 1vw 1vw;
  }
  .interndescription {
    font-size: 3vw;
    padding: 6vw 3vw 0vw 3vw;
  }
  .teamListTitle {
    font-size: 3vw;
    padding: 3vw 0vw 0vw 0vw;
  }
  .gridContainer4 {
    padding: 0vw 1vw;
    padding-left: 0vw;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }
  .teamList {
    font-size: 3vw;
    padding-top: 1vw;
  }
  .applyButton {
    width: 35vw;
    font-size: 3.5vw;
    padding: 1vw 1vw;
    margin-top: 3vw;
  }
}
